@import "../../assets/styles/variables";
@import "../../assets/styles/media";
@import "../../assets/styles/adaptedProperties";

.check {
  width: calc(287 * 100% / 1248);
  padding-left: calc(32 * 100% / 1248);
  flex-grow: 1;
  display: flex;
  flex-direction: column; }

.title {
  text-align: end;
  margin-bottom: 26px; }

@include desktopMinDevice {
  .check {
    width: 35%;
    padding-left: 5%; } }

@include tabletDevice {
  .check {
    width: 100%;
    padding-top: 0;
    padding-left: 0;
    justify-content: flex-end;
    flex-grow: 0; }

  .dots {
    width: 300px; }

  .data {
    position: absolute;
    height: 0;
    width: 100%;
    bottom: 0;
    left: 0;
    right: 0;
    background: $white;
    padding: 0 32px;
    box-sizing: border-box;
    // display: none
    transform: translateY(100%);
    transition: all $medium ease;
    z-index: -2;
    opacity: 0;
    overflow: scroll; }


  .toggle {
    height: 100%;
    padding: 32px;
    z-index: 1;
    opacity: 1;
    transform: translateY(0); } }


@include mobileDevice {
  .check {
    padding-bottom: 32px;
    flex-grow: 0; }

  .title {
    @include adaptedForMobile(font-size, 12, 17, px );
    text-align: start;
    margin-bottom: 0px; } }
